body {
  font-family: SUSE;
  margin: 0;
  padding: 20px;
  position: relative;
  background-color: rgb(44, 155, 143);
  /*background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  overflow: auto; */
}

body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.3);
  z-index: -1;
}

input.search-input, select.filter-select {
  padding: 8px;
  margin: 10px 0;
  width: 200px;
  border-radius: 4px;
  border: 2px solid black;
  background-color: #fff;
  transition: all 0.3s ease;
}

input.search-input:hover, select.filter-select:hover { 
  background-color: #ffeb99;
  border-color: #ffcc00;
}

button {
  padding: 10px 20px; 
  background-color: rgb(44, 155, 143); 
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

button:hover {
  background-color: #010f02;
  transform: scale(1.05);
}

header.h1, p {
  font-size: 2.6vh;
}

.ingredient-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  font-size: 2.5vh; /* FONT SIZE INGREDIENT */ 
  background-color: transparent;
  
}

.ingredient-table th,
.ingredient-table td {
  padding: 10px;
  text-align: left;
  vertical-align: middle;
  border: 2px solid black;
  background-color: transparent;
}

.ingredient-box {
  background-color: #4d0c0c;
  color: #333;
  padding: 10px;
  border-radius: 8px;
  text-align: center;
  border: 2px solid #ddd;
  transition: all 0.3s ease; 
}

.ingredient-box:hover {
  background: #ffd54f;
  color: black;
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.category-box {
  color: black;
  padding: 10px;
  border-radius: 4px;
  text-align: center;
  border: 2px solid black;
  font-size: 2.2vh;
}

.rank-box {
  text-align: center;
  padding: 10px;
  font-size: 5vh;
  border-radius: 4px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
}

.rank-box.rank-0 {
  color: #4caf50; 
}

.rank-box.rank-1 {
  color: #fbc02d;
}

.rank-box.rank-2 {
  color: #ff9800;
}

.rank-box.rank-3 {
  color: #f44336;
}

.ingredient-table th {
  background-color: #00796b;
  color: white;
  font-weight: bold;
  font-size: 2.2vh;
  padding: 15px;
  border: 2px solid black;
  box-shadow: inset 0 -4px 0 #004d40;
  cursor: pointer;
  transition: background-color 0.3s ease;
  position: sticky;
  top: 0;
  z-index: 2; /* ensures the header stays above the other content when scolling */
}

.ingredient-table th:hover {
  background-color: #004d40;
}

.ingredient-table tr:hover {
  background-color: #e0f7fa;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.header {
  font-size: 1rem;
  text-align: center;
  margin-bottom: 20px;
}

.compatScape {
  font-size: 1.5rem;
  margin-bottom: 10px;
  text-align: center;
}

.compatibility-chart {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px black solid;
  border-radius: 8px;
  padding: 10px;
  max-width: 600px; /* Restrict width */
  width: 90%; /* Adjust width to be responsive */
  background-color: #f9f9f9;
  margin: 20px auto; /* Center the chart horizontally with auto margins */
}

.compatibility-row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%; /* Ensure rows take up the full width of the container */
  padding: 5px 0;
  border-bottom: 1px solid #ddd;
}

.compatibility-row:last-child {
  border-bottom: none;
}

.compatibility-label {
  font-weight: bold;
  width: 30px;
  margin-right: 10px;
}

.compatibility-label-0 {
  color: #4caf50;
  font-size: 2rem;
}

.compatibility-label-1 {
  color: #fbc02d;
  font-size:  2rem;
}

.compatibility-label-2 {
  color: #ff9800;
  font-size: 2rem;
}

.compatibility-label-3 {
  color: #f44336;
  font-size: 2rem;
}

.compatibility-label-4, .compatibility-label-5 {
  font-size: 2em;
}

.compatibility-description {
  flex: 1;
}

/* Modal backdrop */
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6); /* Darker overlay */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

/* Modal content */
.modal-content {
  background-color: #fff;
  padding: 30px 20px;
  border-radius: 10px;
  text-align: center;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  font-family: 'Open Sans', sans-serif;
}

.modal-content h3 {
  margin-bottom: 20px;
  font-size: 1.2em;
}

.modal-options {
  margin-bottom: 20px;
  text-align: left;
}

.modal-options label {
  display: block;
  margin: 10px 0;
  font-size: 1em;
}

.modal-options input[type='radio'] {
  margin-right: 10px;
}

.modal-buttons {
  display: flex;
  justify-content: center;
}

.modal-buttons button {
  margin: 0 10px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  font-size: 1em;
}

.modal-buttons .save-button {
  background-color: #28a745; /* Green */
  color: #fff;
}

.modal-buttons .cancel-button {
  background-color: #dc3545; /* Red */
  color: #fff;
}

.modal-buttons button:hover {
  opacity: 0.9;
}

.modal-buttons button:focus {
  outline: none;
}


/* Note Modal content */
.note-modal-content {
  background-color: #fff;
  padding: 30px 20px;
  border-radius: 10px;
  text-align: center;
  width: 90%;
  max-width: 500px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  font-family: 'Open Sans', sans-serif;
}

.note-modal-content h3 {
  margin-bottom: 20px;
  font-size: 1.2em;
}

.note-modal-content textarea {
  width: 100%;
  height: 100px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 1em;
  font-family: 'Open Sans', sans-serif;
  border: 1px solid #ccc;
  border-radius: 5px;
  resize: vertical;
}

.note-modal-content .modal-buttons {
  display: flex;
  justify-content: center;
}

.note-modal-content button {
  margin: 0 10px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  font-size: 1em;
}

.note-modal-content .save-button {
  background-color: #007bff; /* Blue */
  color: #fff;
}

.note-modal-content .cancel-button {
  background-color: #6c757d; /* Gray */
  color: #fff;
}

.note-modal-content button:hover {
  opacity: 0.9;
}

.note-modal-content button:focus {
  outline: none;
}

.note-text {
  cursor: pointer;
  text-decoration: underline;
  color: blue;
}

@media screen and (max-width: 480px) {
  /* Adjust header font size */
  .header h1 {
    font-size: 1.2em;
  }

  /* Stack table into a card layout */
  .ingredient-table,
  .ingredient-table thead,
  .ingredient-table tbody,
  .ingredient-table th,
  .ingredient-table td,
  .ingredient-table tr {
    display: block;
  }

  /* Hide table headers */
  .ingredient-table thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  .ingredient-table tr {
    margin-bottom: 15px;
  }

  .ingredient-table td {
    position: relative;
    padding-left: 50%;
    text-align: left;
    font-size: 0.9em;
  }

  .ingredient-table td:before {
    position: absolute;
    top: 0;
    left: 10px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    font-weight: bold;
  }

  /* Labels for each data cell */
  .ingredient-table td:nth-of-type(1):before { content: "Ingredient"; }
  .ingredient-table td:nth-of-type(2):before { content: "Category"; }
  .ingredient-table td:nth-of-type(3):before { content: "Rank"; }
  .ingredient-table td:nth-of-type(4):before { content: "Status"; }
  .ingredient-table td:nth-of-type(5):before { content: "Notes"; }
  .ingredient-table td:nth-of-type(6):before { content: "H!"; }
  .ingredient-table td:nth-of-type(7):before { content: "H"; }
  .ingredient-table td:nth-of-type(8):before { content: "A"; }
  .ingredient-table td:nth-of-type(9):before { content: "L"; }
  .ingredient-table td:nth-of-type(10):before { content: "B";  }
  .ingredient-table td:nth-of-type(11):before { content: "Remarks"; }

  /* Adjust modal sizes */
  .modal-content {
    width: 90%;
    padding: 15px;
  }

  /* Adjust input and textarea sizes */
  .modal-content textarea,
  .modal-content input {
    width: 100%;
    font-size: 1em;
  }

  /* Adjust button sizes */
  .modal-buttons button {
    font-size: 1em;
    padding: 8px 16px;
  }
}

.info-box {
  margin: 20px auto;
  padding: 15px;
  max-width: 600px;
  background-color: rgb(4, 155, 143);
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.info-box ul {
  list-style-type: disc;
  padding-left: 20px;
  text-align: left;
}

.info-box li {
  margin-bottom: 10px;
  font-size: 1.2em;
}
